.FooterCont {
  display: flex;
  justify-content: space-evenly;
  margin: 31px 0 21px 0;
  background: #172337;
  height: 200px;
}
.FooterAdd,
.FooterDet {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
h6 {
  font-size: 18px;
  text-decoration: underline red;
}
a {
  text-decoration: none;
  color: #fff;
}

.copy {
  display: flex;
  justify-content: space-between;
  padding: 14px 14px 14px 7px;
  font-size: 18px;
}
