.productImg {
  width: 100%;
  height: 100%;
}
.productCont {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  gap: 11px;
  padding: 11px;
  margin: 100px 28px 50px 28px;
}
.carousel {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
}
.description {
  font-size: 21px;
}
.corouselbutton,
.btn {
  background: none;
  border: 1px solid #fff;
  padding: 5px 10px;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(255, 255, 255, 0.3);
  transition: all 0.3s;
}
.corouselbutton:hover,
.btn:hover {
  background: #7950f2;
  color: #fff;
}
.productBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
}
.productPrice {
  display: flex;
  flex-direction: column;
  padding: 21px;
}
.price {
  font-size: 28px;
  margin-bottom: 18px;
}
.carouselimage {
  max-width: 100%;
  height: auto;
  border-radius: 12px;
  box-shadow: 0 2px 5px rgba(255, 255, 255, 0.3);
  /* margin: 0 10px; */
}
.desCont {
  display: flex;
  flex-direction: column;
  align-items: center;
}
